@import '../../../CommonFiles/commonStyles.module';

.authWrapper {
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   width: 400px;

   .logInto {
      font-size: 22px;
      margin-top: 20px;
      font-weight: 400;
   }

   .captchaUrl {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 10px;
      height: 180px;

      .captchaUrlImage {
         border: 1px solid black;
         border-radius: 10px;
      }

      .captchaInp {
         box-shadow: $boxShadowForBlock;
         border-radius: 10px;
         font-size: 28px;
         padding-left: 10px;
      }
   }

   .authSubWrapper {
      margin-top: 40px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      text-align: left;
      width: 100%;

      .inputWrap {
         width: 90%;
         text-align: center;

         .input {
            border-radius: 7px;
            background-color: $inputBackGround;
            padding-left: 10px;
            width: 100%;
            border: 1px solid #d4d4d4;
            font-size: 22px;
            height: 50px;
            @include onHoverActiveButton;
            box-shadow: $boxShadowForBlockLight;

            &::placeholder {
               opacity: 0.7;
            }
         }

      }

      .showErrorMessage {
         margin-bottom: 5px;
         padding: 5px 0;
         display: flex;
         justify-content: center;
         color: red;
         font-weight: bold;
         opacity: 1;
      }
   }

   .loginFormWrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      .buttonWrap {
         width: 90%;

         .signInButton {
            border-radius: 7px;
            width: 100%;
            height: 50px;
            background-color: $buttonColor;
            color: #fff;
            font-size: 28px;
            @include onHoverActiveButton;
         }
      }

      .recoverPassWrap {
         .recoverPass {
            display: block;
            margin: 15px auto;
            color: $buttonColor;
            font-size: 18px;
            @include onHoverText;
         }

         .or {
            text-align: center;
            color: #3b5998;
         }
      }

      .createAccountWrap {
         width: 100%;
         margin: 15px 0 20px 0;

         .createAccount {
            display: block;
            width: 60%;
            margin: 0 auto;

            .createAccountButton {
               background-color: #42B72A;
               color: #fff;
               width: 100%;
               height: 50px;
               border-radius: 7px;
               font-size: 20px;
               @include onHoverActiveButton;
            }
         }
      }
   }
}






