.table {
   max-height: 24px;
   padding-top: 3px;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
}

.table_small {
   align-items: center;
}

.small {
   padding: 0 3px;
}

.list {
   padding-left: 10px;

   &:hover {
      -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
      transform: scale(1.2);
      -webkit-transition: 0.1s;
      -o-transition: 0.1s;
      transition: 0.1s;
   }
}

.link {
   color: #ffffff;
}
